import React from 'react'
import { Field, reduxForm } from 'redux-form'
import { Card, CardBody, Button } from 'reactstrap'
import { compose } from 'recompose'
import { withFirebase } from 'react-redux-firebase'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { FormInput, PageHeader, LoadingMessage } from '../interface'
import { notifyError } from '../../actions'
import { AuthToggler } from './'

class AuthLogin extends React.Component {

  state = { processing: false }

  onSubmit = async (values) => {

    this.setState({processing: true})

    const { firebase } = this.props

    if(values.email && values.password) {
      await firebase.login(values).catch(error => this.props.notifyError(error.message))
      this.setState({processing: false})
    }
    else {
      this.props.notifyError('Usurio ou senha faltando')
      this.setState({processing: false})
    }
  }

  render()  {

    return (<div>
              <PageHeader title="Login" />
                <Card>
                  <CardBody>
                    <form onSubmit={this.props.handleSubmit(this.onSubmit.bind(this))} >

                      <AuthToggler mode="login" />

                      <Field
                        name="email"
                        label="E-mail"
                        type="text"
                        placeholder="E-mail"
                        component={FormInput}
                      />

                      <Field
                        name="password"
                        label="Senha"
                        type="password"
                        placeholder="Senha (mn. 6 caracteres)"
                        component={FormInput}
                      />

                      <div className="my-3 d-flex justify-content-between align-items-center">
                        <Button color="primary" >Prosseguir</Button>
                        { this.state.processing ? <LoadingMessage /> : '' }
                        <Link to="/auth/reset" >Recuperar senha</Link>
                      </div>

                    </form>
                  </CardBody>
                </Card>
              </div>)
  }
}

function validate(values) {
  const errors = {}

  if(!values.email) {
    errors.email = 'Por favor insira um e-mail'
  }

  if(!values.password) {
    errors.password = 'Por favor insira senha'
  }

  return errors
}

AuthLogin = connect(null, { notifyError })(AuthLogin)
AuthLogin = compose(withFirebase, reduxForm({validate, form: 'AuthLoginForm'}) )(AuthLogin)

export { AuthLogin }



